interface Tile {
  offset: Vec;
  image_offset: Vec;
}

interface Tetronimo {
  pos: Vec;
  sprite: HTMLImageElement;
  state: number;
  size: number;
  tiles: Tile[];
  rotation_offset: number;
}

interface Player {
  pos: Vec;
  vel: Vec;
  jumping: boolean;
  jump_buffer: number;
  facing: number;
  grounded: boolean;
  ceilinged: boolean;
  target: Tetronimo | null;
  push_cooldown: number;
  rotate_cooldown: number;
}

