function new_falling() {
  if (queue.length == 0) {
    let pool = [Tetronimo.I, Tetronimo.J, Tetronimo.L, Tetronimo.O, Tetronimo.S, Tetronimo.T, Tetronimo.Z];
    while (pool.length > 0) {
      let proto = pool.splice(Math.floor(Math.random() * pool.length), 1)[0];
      queue.push(proto);
    }
  }

  let proto = queue.shift()!;
  tetronimos.push({
    pos: new Vec(5, 0),
    size: proto.size,
    sprite: proto.sprite,
    tiles: proto.shape.map((v: Vec, i: number) => ({ offset: v, image_offset: proto.images[i] })),
    state: 0,
    rotation_offset: proto.rotation_offset,
  });
}

function draw_tetronimo(t: Tetronimo) {
  for (let tile of t.tiles) {
    let o = Vec.scale(Vec.add(t.pos, tile.offset), 32);

    let angle = -(t.state + t.rotation_offset) / 2 * Math.PI;
    context.translate(o.x + consts.UNIT / 2, o.y + consts.UNIT / 2);
    context.rotate(angle);

    // context.fillRect(-consts.UNIT / 2, -consts.UNIT / 2, consts.UNIT, consts.UNIT);
    context.drawImage(t.sprite,
      tile.image_offset.x * consts.UNIT, tile.image_offset.y * consts.UNIT,
      consts.UNIT, consts.UNIT,
      -consts.UNIT / 2, -consts.UNIT / 2,
      consts.UNIT, consts.UNIT
    );

    context.restore();
    context.resetTransform();
  }
}

const kick_states: { [size: number]: Vec[][] } = {
  3: [
    [new Vec(-1, 0), new Vec(-1, 1), new Vec(0, -2), new Vec(-1, -2)],
    [new Vec(1, 0), new Vec(1, -1), new Vec(0, 2), new Vec(1, 2)],
    [new Vec(1, 0), new Vec(1, 1), new Vec(0, -2), new Vec(1, -2)],
    [new Vec(-1, 0), new Vec(-1, -1), new Vec(0, 2), new Vec(-1, 2)],
  ],
  4: [
    [new Vec(-2, 0), new Vec(1, 0), new Vec(1, 0), new Vec(-2, -1), new Vec(1, 2)],
    [new Vec(-1, 0), new Vec(2, 0), new Vec(-1, 2), new Vec(2, -1)],
    [new Vec(2, 0), new Vec(-1, 0), new Vec(2, 1), new Vec(-1, -2)],
    [new Vec(1, 0), new Vec(-2, 0), new Vec(1, -2), new Vec(-2, 1)],
  ]
}

function rotate(t: Tetronimo, is_right: boolean) {
  let kick_vec = [
    new Vec(0, 0),
    ...(kick_states[t.size]?.[t.state] ?? []),
  ];

  let rotated = [];

  if (is_right) {
    for (let tile of t.tiles) {
      rotated.push({
        offset: new Vec(t.size - 1 - tile.offset.y, tile.offset.x),
        image_offset: tile.image_offset,
      });
    }
  } else {
    for (let tile of t.tiles) {
      rotated.push({
        offset: new Vec(tile.offset.y, t.size - 1 - tile.offset.x),
        image_offset: tile.image_offset,
      });
    }

    kick_vec = kick_vec.map(v => Vec.scale(v, -1));
  }

  let new_state = t.state;
  if (is_right) new_state -= 1;
  else new_state += 1;

  new_state = new_state % 4;
  if (new_state < 0) new_state = 3;

  let old = t.tiles;
  t.tiles = rotated;
  let found = false;

  for (let kick of kick_vec) {
    let result = collide_tetronimo(t, Vec.add(t.pos, kick));
    if (typeof result != 'number' && typeof result != 'boolean') {
      t.state = new_state;
      t.pos = Vec.add(t.pos, kick);
      found = true;
      console.log("no collision");
      break;
    }
  }

  if (!found) {
    console.log("Cannot rotate");
    t.tiles = old;
  }
}

function collide_x(pos: Vec, dir: number, size?: Vec) {
  size = size ?? new Vec(1, 1);

  if (pos.x < 0)
    return -pos.x;

  if (pos.x + size.x > consts.WIDTH)
    return consts.WIDTH - (pos.x + size.x);

  for (let t of tetronimos) {
    for (let tile of t.tiles) {
      let o = Vec.add(t.pos, tile.offset);

      if (o.x < pos.x + size.x && o.x + 1 > pos.x &&
        o.y < pos.y + size.y && o.y + 1 > pos.y) {
        if (dir < 0)
          return (o.x + 1) - pos.x;
        return o.x - (pos.x + size.x);
      }
    }
  }

  return null;
}

function collide_y(pos: Vec, dir: number, size?: Vec) {
  size = size ?? new Vec(1, 1);

  if (pos.y + size.y > consts.HEIGHT)
    return consts.HEIGHT - (pos.y + size.y);

  for (let t of tetronimos) {
    for (let tile of t.tiles) {
      let o = Vec.add(t.pos, tile.offset);

      if (o.x < pos.x + size.x && o.x + 1 > pos.x &&
        o.y < pos.y + size.y && o.y + 1 > pos.y) {
        if (dir < 0)
          return (o.y + 1) - pos.y;
        return o.y - (pos.y + size.y);
      }
    }
  }

  return null;
}

function collide_tetronimo(t: Tetronimo, nextPos: Vec) {
  for (let tile of t.tiles) {
    let pos = Vec.add(nextPos, tile.offset);
    if (pos.y + 1 > consts.HEIGHT) {
      return consts.HEIGHT - (pos.y + 1);
    }

    if (pos.x + 1 > consts.WIDTH || pos.x < 0) {
      return true;
    }

    if (pos.x + 1 > player.pos.x - 0.5 && pos.x < player.pos.x + 0.5
      && pos.y + 1 > player.pos.y - 1 && pos.y < player.pos.y) {
      return true;
    }

    for (let other of tetronimos) {
      if (t == other) continue;

      for (let tile of other.tiles) {
        let o = Vec.add(other.pos, tile.offset);

        if (o.x >= pos.x + 1 || o.x + 1 <= pos.x) { // x not overlap
          continue;
        }
        if (o.y + 1 <= pos.y || pos.y + 1 <= o.y) { // y not overlap
          continue;
        } else {
          if (o.y < pos.y + 1) {
            let result = o.y - (pos.y + 1);
            return result;
          } else if (pos.y < o.y + 1) {
            let result = pos.y - (o.y + 1);
            return result;
          }
        }
      }
    }
  }

  return null;
}

function find_collision(t: Tetronimo, offset?: Vec): [Tetronimo, Tile, Tile] | null;
function find_collision(pos: Vec, size: Vec, skip?: Tetronimo): [Tetronimo, Tile] | null;
function find_collision(a: any, b?: any, c?: Tetronimo): any {
  if (a instanceof Vec) {
    let pos: Vec = a;
    let size: Vec = b;

    for (let t of tetronimos) {
      if (t == c) continue;

      for (let tile of t.tiles) {
        let o = Vec.add(t.pos, tile.offset);

        if (o.x < pos.x + size.x && o.x + 1 > pos.x &&
          o.y < pos.y + size.y && o.y + 1 > pos.y) {
          return [t, tile];
        }
      }
    }
  } else {
    let t: Tetronimo = a;
    let offset = b ?? new Vec(0, 0);

    let origin = Vec.add(t.pos, offset);

    for (let tile of t.tiles) {
      let o = Vec.add(origin, tile.offset);
      let collision = find_collision(o, new Vec(1, 1), t);
      if (collision) return [...collision, tile];
    }
  }

  return null;
}

function start_jump() {
  player.jump_buffer = 0;

  player.vel = new Vec(player.vel.x, -consts.JUMP_SPEED);
}

