input.onJump(() => {
  if (player.grounded) {
    start_jump();
  } else {
    player.jump_buffer = consts.BUFFER_TIME;
  }
});

input.onRotate(() => {
  do_rotate();
});

function do_rotate() {
  if (player.target == null) return;
  if (player.rotate_cooldown > 0) return;

  rotate(player.target, player.facing < 1);
}

function find_full_lines() {
  let arr = [];

  for (let i = 0; i < consts.HEIGHT; i++) {

    let row_filled = true;
    for (let j = 0; j < consts.WIDTH; j++) {

      let found = false;
      for (let tet of tetronimos) {

        if (!found) {
          for (let tile of tet.tiles) {
            let pos = Vec.add(tet.pos, tile.offset);
            if (pos.x == j && pos.y == i) {
              found = true;
              break;
            }
          }
        }
      }
      if (!found) {
        row_filled = false;
        break;
      }
    }
    if (row_filled) arr.push(i);
  }

  if (arr.length > 0) {
    for (let i of arr) {
      console.log("Line " + i.toString() + " is full");
    }
  }

  return arr;
}

function clear_lines(lines: number[]) {

  for (let i = 0; i < lines.length; i++) {
    for (let j = tetronimos.length - 1; j >= 0; j--) {
      for (let k = tetronimos[j].tiles.length - 1; k >= 0; k--) {
        if (tetronimos[j].tiles[k].offset.y + tetronimos[j].pos.y == lines[i]) {
          tetronimos[j].tiles.splice(k, 1);
        }
      }
      if (tetronimos[j].tiles.length == 0) {
        tetronimos.splice(j, 1);
      }
    }
  }
}

