function tick(time: number) {
  let cancel = requestAnimationFrame(tick); // keep going forever

  score_ctx.font = "bold 20px Orbitron";
  score_ctx.textAlign = "center";
  score_ctx.fillStyle = "black";
  score_ctx.fillText("Score: " + score.toString(), consts.SCORE_ABSOLUTE_WIDTH / 2, consts.SCORE_ABSOLUTE_HEIGHT / 2);

  time /= 1000;
  let delta = previous ? time - previous : 0;
  previous = time;

  let landed = false;
  for (let t of tetronimos.slice().reverse()) {
    let nextPos = Vec.add(t.pos, new Vec(0, delta * consts.FALL_SPEED));
    let collision = collide_tetronimo(t, nextPos);

    if (typeof collision == 'number') {
      nextPos = Vec.add(nextPos, new Vec(0, collision));

      if (tetronimos[tetronimos.length - 1] == t) {
        landed = true;
      }
    }

    let oldPos = t.pos;
    t.pos = nextPos;

    let hit = collide_y(Vec.add(player.pos, new Vec(-0.5, -1)), -1, new Vec(1, 1));
    if (hit) {
      if (player.grounded) {
        t.pos = oldPos;
      } else {
        player.vel = new Vec(player.vel.x, Math.max(0, player.vel.y));
        player.pos = Vec.add(player.pos, new Vec(0, hit));
      }
    }
  }

  let lines = find_full_lines();

  clear_lines(lines);

  if (landed) {
    new_falling();
  }

  player.grounded = false;
  player.ceilinged = false;

  { // move & slide
    let next = Vec.add(player.pos, new Vec(player.vel.x * delta, 0));
    let hit = collide_x(Vec.add(next, new Vec(-0.5, -1)), player.vel.x, new Vec(1, 1));
    if (hit) {
      player.vel = new Vec(0, player.vel.y);
      next = Vec.add(next, new Vec(hit, 0));
    }

    next = Vec.add(next, new Vec(0, player.vel.y * delta));
    hit = collide_y(Vec.add(next, new Vec(-0.5, -1)), player.vel.y, new Vec(1, 1));
    if (hit) {
      player.vel = new Vec(player.vel.x, 0);
      next = Vec.add(next, new Vec(0, hit));
      player.grounded = hit < 0;
      player.ceilinged = hit > 1;
    }

    player.pos = next;
  }

  { // jumping
    if (!player.jumping && player.grounded && player.jump_buffer > 0) {
      start_jump();
    }

    player.jump_buffer = Math.max(0, player.jump_buffer - delta);
    player.vel = new Vec(player.vel.x, Math.min(player.vel.y + consts.GRAVITY * delta, consts.MAX_FALL_SPEED));
  }

  { // running
    if (player.push_cooldown > 0) {
      player.push_cooldown -= delta;
    } if (player.rotate_cooldown > 0) {
      player.rotate_cooldown -= delta;
    }

    let pos;
    if (player.facing > 0)
      pos = Vec.add(player.pos, new Vec(0.5, -1));
    else if (player.facing < 0)
      pos = Vec.add(player.pos, new Vec(-1, -1));
    else
      pos = null;

    let collision = pos && find_collision(pos, new Vec(0.5, 1));
    player.target = collision?.[0] ?? null;

    if (player.target) {
      if (player.push_cooldown <= 0 && input.isPushing()) {
        let oldPos = player.target.pos;
        player.target.pos = new Vec(oldPos.x + player.facing, oldPos.y);

        let valid = !find_collision(player.target) && player.target.tiles.every(t => {
          let pos = Vec.add(player.target!.pos, t.offset);
          return pos.x >= 0 && pos.x < consts.WIDTH;
        });

        if (valid) {
          player.push_cooldown = consts.MOVE_COOLDOWN;
        } else {
          player.target.pos = oldPos;
        }
      }
    }

    let speed_target;
    if (input.isMoving(1)) {
      player.facing = 1;
      speed_target = consts.RUNNING_SPEED_LIMIT;
    } else if (input.isMoving(-1)) {
      player.facing = -1;
      speed_target = -consts.RUNNING_SPEED_LIMIT;
    } else
      speed_target = 0;

    let speed_change;
    if (Math.abs(player.vel.x) > consts.RUNNING_SPEED_LIMIT && Math.sign(player.vel.x) == Math.sign(speed_target)) {
      if (player.grounded)
        speed_change = consts.GROUND_DECELERATION;
      else
        speed_change = consts.AIR_DECELERATION;
    } else {
      if (player.grounded)
        speed_change = consts.GROUND_RUNNING_ACCELERATION;
      else if (Math.sign(player.vel.x) == -Math.sign(speed_target))
        speed_change = consts.AIR_RUNNING_ACCELERATION;
      else
        speed_change = consts.AIR_DECELERATION;
    }

    if (speed_target > player.vel.x) {
      player.vel = new Vec(Math.min(player.vel.x + speed_change * delta, speed_target), player.vel.y);
    } else {
      player.vel = new Vec(Math.max(player.vel.x - speed_change * delta, speed_target), player.vel.y);
    }
  }

  context.clearRect(0, 0, consts.WIDTH * consts.UNIT, consts.HEIGHT * consts.UNIT);

  for (let t of tetronimos) {
    draw_tetronimo(t);
  }

  context.fillStyle = 'black';
  context.fillRect((player.pos.x - 0.5) * consts.UNIT, (player.pos.y - 1) * consts.UNIT, consts.UNIT, consts.UNIT);

  context.fillStyle = '#ff000080';
  if (player.facing > 0)
    context.fillRect((player.pos.x + 0.5) * consts.UNIT, (player.pos.y - 1) * consts.UNIT, consts.UNIT * 0.5, consts.UNIT);
  else if (player.facing < 0)
    context.fillRect((player.pos.x - 1) * consts.UNIT, (player.pos.y - 1) * consts.UNIT, consts.UNIT * 0.5, consts.UNIT);
}

input.onStart(() => {
  if (in_menu) {
    player = {
      target: null,
      facing: 0,
      jumping: false,
      jump_buffer: 0,
      pos: new Vec(5, 20),
      vel: new Vec(0, 0),
      grounded: false,
      ceilinged: false,
      push_cooldown: 0,
      rotate_cooldown: 0,
    };

    new_falling();
    in_menu = false;
    previous = 0;
    score = 0;

    background_music.volume = 0.10;
    background_music.play();
  }
});

function start_screen(time: number) {
  if (!in_menu) {
    tick(time);
  } else {
    requestAnimationFrame(start_screen); // keep going forever

    time /= 1000;
    let delta = previous ? time - previous : 0;
    previous = time;

    context.clearRect(0, 0, consts.WIDTH * consts.UNIT, consts.HEIGHT * consts.UNIT);
    let high_score = cookie.get_high_score();

    let middle_x = consts.ABSOLUTE_WIDTH / 2;

    let top_y = consts.ABSOLUTE_HEIGHT / 4;

    let middle_y = consts.ABSOLUTE_HEIGHT / 4 * 2;

    let bottom_y = consts.ABSOLUTE_HEIGHT / 4 * 3;

    context.fillStyle = "black";
    context.textAlign = "center";
    context.font = "bold 30px Orbitron";
    context.fillText("Squishy Fruit Mayhem", middle_x, top_y, 300);

    context.fillStyle = "black";
    context.textAlign = "center";
    context.font = "bold 24px Orbitron";
    context.fillText("High Score: " + high_score.toString(), middle_x, middle_y);

    if (time % 2 < 1) {
      context.font = "bold 20px Orbitron";
      context.fillStyle = "black";
      context.fillText("Press Enter to Begin", middle_x, bottom_y);
    } else {
      context.font = "bold 20px Orbitron";
      context.fillStyle = "orange";
      context.fillText("Press Enter to Begin", middle_x, bottom_y);
    }
  }
}

export async function init(canvas: HTMLCanvasElement, canvas_score: HTMLCanvasElement) {
  canvas_score.width = consts.SCORE_ABSOLUTE_WIDTH;
  canvas_score.height = consts.SCORE_ABSOLUTE_HEIGHT;
  canvas_score.style.width = `${consts.SCORE_ABSOLUTE_WIDTH}px`;
  canvas_score.style.height = `${consts.SCORE_ABSOLUTE_HEIGHT}px`;

  canvas.width = consts.ABSOLUTE_WIDTH;
  canvas.height = consts.ABSOLUTE_HEIGHT;
  canvas.style.width = `${consts.ABSOLUTE_WIDTH}px`;
  canvas.style.height = `${consts.ABSOLUTE_HEIGHT}px`;

  await loaded;

  context = canvas.getContext('2d')!;
  score_ctx = canvas_score.getContext('2d')!;
  assert(context != null, 'no context');

  requestAnimationFrame(start_screen);
}

background_music.addEventListener('ended', function () {
  this.currentTime = 0;
  this.play();
}, false);
