<template>
  <div class="root">
    <canvas class="game" ref="canvas" />
    <canvas class="score" ref="canvas_score" />
  </div>
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue';

import { init } from './game';

export default {
  name: 'game',

  setup() {
    let canvas = ref(null);
    let canvas_score = ref(null);

    onMounted(() => {
      init(canvas.value, canvas_score.value);
    });

    return { canvas, canvas_score };
  },
};
</script>

