
export function set_high_score(score: number) {
    set_cookie("High Score", score.toString());
}

export function get_high_score() {
    let result = get_cookie("High Score");
    if(result == null) {
        set_high_score(0);
        return 0;
    }
    return parseFloat(result);
}

export function reset_highscore() {
    set_high_score(0);
}

export function set_cookie(name: string, val: string) {
    const date = new Date();
    const value = val;

    // Set it expire in 7 days
    date.setTime(date.getTime() + (7 * 24 * 60 * 60 * 1000));

    // Set it
    document.cookie = name+ "=" +value+ "; expires=" + date.toUTCString() + "; path=/";
}

export function get_cookie(name: string) {
    let value = "; " + document.cookie;
    let parts = value.split("; " + name + "=");

    if(parts == undefined || parts == null) return null;
    
    else if (parts.length == 2) {
        let first = parts.pop();
        if(first != undefined) return first.split(";").shift();
        else return null;
    }

    return null;
}

export function delete_cookie(name: string) {
    const date = new Date();

    // Set it expire in -1 days
    date.setTime(date.getTime() + (-1 * 24 * 60 * 60 * 1000));

    // Set it
    document.cookie = name+"=; expires="+date.toUTCString()+"; path=/";
}