export const 
    UNIT = 32,
    WIDTH = 10,
    HEIGHT = 20,
    SCORE_WIDTH = 10,
    SCORE_HEIGHT = 10,
    ABSOLUTE_WIDTH = WIDTH * UNIT,
    ABSOLUTE_HEIGHT = HEIGHT * UNIT,
    SCORE_ABSOLUTE_WIDTH = SCORE_WIDTH * UNIT,
    SCORE_ABSOLUTE_HEIGHT = SCORE_HEIGHT * UNIT,
    MOVE_COOLDOWN = 0.5,
    FALL_SPEED = 2,
    BUFFER_TIME = 0.1,
    MAX_FALL_SPEED = 320 / 8,
    RUNNING_SPEED_LIMIT = 90 / 8,    
    GROUND_RUNNING_ACCELERATION = 3000 / 8,
    GROUND_DECELERATION = 400,   
    AIR_RUNNING_ACCELERATION = 1800 / 8,
    AIR_DECELERATION = 320,
    JUMP_TIME = 0.3,
    JUMP_HEIGHT = 3.2,
    GRAVITY = (2 * JUMP_HEIGHT) / (JUMP_TIME * JUMP_TIME),
    JUMP_SPEED = GRAVITY * JUMP_TIME;
