import { A, ENTER, isKeyDown, LEFT, onKeyDown, RIGHT, S, SPACE, X, Z } from "./keyboard";

const jump: (() => void)[] = [];
const start: (() => void)[] = [];
const rotate: (() => void)[] = [];

function dispatch(list: (() => void)[]) {
  for (let fn of list) {
    fn();
  }
}

export function onJump(fn: () => void) {
  jump.push(fn);
}

export function onStart(fn: () => void) {
  start.push(fn);
}

export function onRotate(fn: () => void) {
  rotate.push(fn);
}

export function isPushing() {
  if (isKeyDown(Z) || isKeyDown(A))
    return true;

  let gp = navigator.getGamepads()[0];
  return gp && gp.buttons[3].pressed;
}

export function isMoving(dir: 1 | -1) {
  if (dir == 1) {
    if (isKeyDown(RIGHT))
      return true;

    let gp = navigator.getGamepads()[0];
    return gp && gp.axes[0] > 0.5;
  }

  if (isKeyDown(LEFT))
    return true;

  let gp = navigator.getGamepads()[0];
  return gp && gp.axes[0] < -0.5;
}

onKeyDown(SPACE, () => dispatch(jump));
onKeyDown(ENTER, () => dispatch(start));
onKeyDown(S, () => dispatch(rotate));
onKeyDown(X, () => dispatch(rotate));

let isJump = false;
let isStart = false;
let isRotate = false;

function connect(button: GamepadButton, list: (() => void)[], was: boolean): boolean {
  let value = button.pressed;

  if (value && !was) {
    dispatch(list);
  }

  return value;
}

requestAnimationFrame(tick);
function tick() {
  requestAnimationFrame(tick);


  let gp = navigator.getGamepads()[0];
  if (gp) {
    console.log(gp.axes.map(p => p));
    console.log(gp.buttons.map(p => p.pressed));

    isJump = connect(gp.buttons[1], jump, isJump);
    isStart = connect(gp.buttons[1], start, isStart);
    isRotate = connect(gp.buttons[0], rotate, isRotate);
  }
}
